'use strict';
import { DECORATE_HOUSE, STAR } from '@/config/image.js';
import { SHOP_HOME } from '@/config/router.js';
import { getShopList } from '@/api/decorateHome.js';
import { getStorage, setStorage } from '@/utils/storage.js';
const { AUTH } = require('../../../config/router.js');

var _self;
export default {
	data() {
		return {
			img: [DECORATE_HOUSE, STAR],
			dataList: [],
			query: {
				name: ''
			},
			showAuth:false,
			imgShow:false,
			star:[
				'starOne',
				'starTwo',
				'starThree',
				'starFour',
				'starFive',
				'starSix',
				'starSeven',
				'starEight',
				'starNine',
				'starTen',
				'starElever'
			]
		};
	},
	methods: {
		
		imgshow(){
			_self.imgShow=true
		},
		search() {
			this.getList();
		},
		linkTo(item) {
			let e = JSON.stringify(item);
			uni.navigateTo({
				url: `${SHOP_HOME}?item=${e}`
			});
		},
		getList() {
			getShopList(this.query).then(res => {
				console.log(res);
				if(res.list){
					_self.dataList = res.list.map(res => {
						let a = [];
						let b = [];
						if(res.lableList){
							res.lableList.map(res => {
								if (res.type == '1') {
									a.push(res);
								}
								if (res.type == '2') {
									b.push(res);
								}
							});
						}
						
					
						return {
							...res,
							picLable: b,
							noPicLable: a
						};
					});
				}
				
				console.log(_self.dataList);
			});
		}
	},
	onLoad() {
		_self = this;
		this.getList();

		const isLogin = getStorage('isLogin');
		if (isLogin) {
			this.showAuth = true;
		} else {
			let pages = getCurrentPages();
			if (pages.length > 0 && AUTH.indexOf('/' + pages[pages.length - 1].route) === 0) return;
			uni.redirectTo({
				url: `${AUTH}?name=${'decorateHouse'}`
			});
		}
	}
};
